import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import ItemList from '../views/itemList'
import Details from '../views/details'

export default new Router({
    // mode: 'history',
    history: false, // 路由后面加了#
    scrollBehavior: (to, from, savedPosition) => {
        if (savedPosition) {
            return savedPosition
        } else {
            return { x: 0, y: 0 }
        }
    },
    routes: [{
        path: '/',
        component: ItemList
    }, {
        path: '/details/:id',
        component: Details
    }, {
        path: '*',
        redirect: '/'
    }]
})


